
.panel{
  border: none;
}
.user-wrap{
  text-align: center;
  margin-bottom: 15px;
}
.username a{
  font-size: 25px;
}
#wrap-card{
    border: 1px solid #ddd;
}
#wrap-card input{
    padding: 5px;
    background-color: #24292e;
    border: none;
    width: 100%;
    color: #ccc;
}
.avatar{
    width: 200px;
    height: 200px;
    margin: 0 auto;
    margin-bottom: 20px;
    margin-top: 20px;
}
.avatar img{
    width: 200px;
    height: auto;
    border-radius: 15%;
    margin: 0;
}
.content-wrap{
    padding: 20px;
    background-color: #24292e;
    color: #fff;
}
.content-wrap ul{
    list-style: none;
    padding: 0;
}
.content-wrap ul li{
    display: inline-block;
    width: 30%;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
}
.content-wrap ul li i{
    display: block;
}



